.Econtainer {

    background-image: url('../../../assets/esg1.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Etitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}
.point {
    font-size:6em
}
.EsectionTitle {
    width: 50em;
    margin-inline: auto;
}
.Esubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
}

.Etext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.E5;
    text-align: left;
}

@media (max-width: 768px) {
    .Econtainer {
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .EsectionTitle {
        width: 21.8em;
    }
    .point {
        font-size:4em
    }
    .Etitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Esubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.E5em;
    }
}

/* for services */
.Eservices-outer-container {
    background-color: #3c685a;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.Eservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    max-width: 800px;
    margin: auto;
    color: white;
}

.Eservice {
    margin-bottom: 30px;
}

.Eservice h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.Eservice p {
    font-size: 16px;
    line-height: 1.E5;
}

@media (max-width: 768px) {
    .Eservices-container {
        padding: 10px;
    }

    .Eservice h2 {
        font-size: 20px;
    }

    .Eservice p {
        font-size: 14px;
    }
}

/* for the image */
.Etecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 768px) {
    .Etecimage {
        width: 101%;
    }
}

/* for the button */
.Econtact {
    font-family: ui-sans-serif;
    display: flex;
    margin-inline: auto;
    background-color: whitesmoke;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}