.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: transparent;
    flex-wrap: wrap;
}
  
.nav-left, .nav-center, .nav-right {
    display: flex;
    align-items: center;
}
.homebutton{
        font-family: ui-sans-serif;   
        top: 30px;
        right: 30px;
        background-color: whitesmoke;
        border: none;
        padding: 10px 20px;
        cursor: pointer;
        font-size: 16px;

}
.nav-center {
    margin-right: 190px
}
  
a {
    color: white;
    margin: 0 10px;
    text-decoration: none;
}

.nav-left a:not(:last-child) {
    margin-right: 30px;
    text-transform: uppercase;
}
  
.nav-right {
    margin-right: 40px;
}
  
a:hover {
    text-decoration: underline;
}
  
.nav-center img {
    height: 50px;
    margin: 0 20px;
}
  
/* Per dispositivi con una larghezza massima di 768px */
@media (max-width: 768px) {
    .navbar {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  
    .nav-left, .nav-center, .nav-right {
      margin: 10px 0;
      justify-content: center;
      width: 100%;
    }
  
    .nav-center img {
      margin: 10px 0;
    }
  
    a {
      margin: 5px;
    }
}
