.banner-container {
  position: relative;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.contact-button {
  font-family: ui-sans-serif;
  position: absolute;
  top: 30px;
  right: 30px;
  background-color: whitesmoke;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: whitesmoke;
  font-family: "Georgia, serif";
}

.logo {
  width: 150px;
  height: auto;
}

h1 {
  margin: 20px 0;
  font-size: 7em;
}

h7 {
  font-size: 2em;
  margin-top: -10px; /* Adjust according to your design */
}

@media (max-width: 768px) {
  .logo {
    width: 100px;
  }
  
  h1 {
    font-size: 2em;
  }
  
  h7 {
    font-size: 1em;
  }
}
