.Ccontainer {

    background-image: url('../../../assets/consulting2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Ctitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}
.point {
    font-size:6em
}
.CsectionTitle {
    width: 50em;
    margin-inline: auto;
}
.Csubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
}

.Ctext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.C5;
    text-align: left;
}

@media (max-width: 768px) {
    .Ccontainer {
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .CsectionTitle {
        width: 21.8em;
    }
    .point {
        font-size:4em
    }
    .Ctitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Csubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.C5em;
    }
}

/* for services */
.Cservices-outer-container {
    background-color: #5b5b59;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.Cservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    max-width: 800px;
    margin: auto;
    color: white;
}

.Cservice {
    margin-bottom: 30px;
}

.Cservice h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.Cservice p {
    font-size: 16px;
    line-height: 1.C5;
}

@media (max-width: 768px) {
    .Cservices-container {
        padding: 10px;
    }

    .Cservice h2 {
        font-size: 20px;
    }

    .Cservice p {
        font-size: 14px;
    }
}

/* for the image */
.Ctecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 768px) {
    .Ctecimage {
        width: 101%;
    }
}

/* for the button */
.Ccontact {
    font-family: ui-sans-serif;
    display: flex;
    margin-inline: auto;
    background-color: whitesmoke;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}