.Lcontainer {

    background-image: url('../../../assets/luxury2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.Ltitle {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;
}
.point {
    font-size:6em
}
.LsectionTitle {
    width: 50em;
    margin-inline: auto;
}
.Lsubtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
}

.Ltext {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.C5;
    text-align: left;
}

@media (max-width: 768px) {
    .Lcontainer {
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .LsectionTitle {
        width: 21.8em;
    }
    .point {
        font-size:4em
    }
    .Ltitle {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .Lsubtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.L5em;
    }
}

/* for services */
.Lservices-outer-container {
    background-color: #5b5b59;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.Lservices-container {
    padding: 20px;
    font-family: ui-sans-serif;
    max-width: 800px;
    margin: auto;
    color: white;
}

.Lservice {
    margin-bottom: 30px;
}

.Lservice h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.Lservice p {
    font-size: 16px;
    line-height: 1.L5;
}

@media (max-width: 768px) {
    .Lservices-container {
        padding: 10px;
    }

    .Lservice h2 {
        font-size: 20px;
    }

    .Lservice p {
        font-size: 14px;
    }
}

/* for the image */
.Ltecimage {
    width: 101%;
    margin-left: -4px;
}

@media (max-width: 768px) {
    .Ltecimage {
        width: 101%;
    }
}

/* for the button */
.Lcontact {
    font-family: ui-sans-serif;
    display: flex;
    margin-inline: auto;
    background-color: whitesmoke;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}