.container {

    background-image: url('../../../assets/technology.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
}

.title {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;

}

.sectionTitle {
    width: 50em;
    margin-inline: auto;
}
.subtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.2em;
    margin-top: 20px;
    text-align: center;
}

.text {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.C5;
    text-align: left;
}

@media (max-width: 768px) {
    .container {
        font-family: 'ui-sans-serif';
        padding: 20px;
    }
    .sectionTitle {
        width: 21.8em;
    }
    .title {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .subtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.5em;
    }
}

/* for services */
.services-outer-container {
    background-color: #1F2A44;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.services-container {
    padding: 20px;
    font-family: ui-sans-serif;
    max-width: 800px;
    margin: auto;
    color: white;
}

.service {
    margin-bottom: 30px;
}

.service h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.service p {
    font-size: 16px;
    line-height: 1.5;
}

@media (max-width: 768px) {
    .services-container {
        padding: 10px;
    }

    .service h2 {
        font-size: 20px;
    }

    .service p {
        font-size: 14px;
    }
}

/* for the image */
.tecimage {
    width: 100%;
    margin-left: -4px;
}

@media (max-width: 768px) {
    .tecimage {
        width: 100%;
    }
}

/* for the button */
.contact {
    font-family: ui-sans-serif;
    display: flex;
    margin-inline: auto;
    background-color: whitesmoke;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}